import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

Vue.use(Vuex);

const vuexPersist = new VuexPersistence({
	storage: window.localStorage
});

const store = new Vuex.Store({
	state: {
		test: "Test"
	},
	mutations: {
		setTest(state, data) {
			state.test = data;
		}
	},
	getters: {
		test(state) {
			return state.test;
		}
	},
	plugins: [
		vuexPersist.plugin
	]
});

export default store;